import { Games } from './games.js'

const games = new Games()

export class UI {


  constructor() {

  }

  displayGames(data) {

    const gamesHTML = data.map((game) =>
      `
<div
   id=${game.id} class="card  bg-transparent group bg-opacity-25 rounded-2xl border border-[#212529]  hover:scale-105 transition-all duration-500 after:content-[''] after:w-full after:h-full after:bg-black after:block after:absolute after:top-0 after:right-0 after:bg-opacity-5 hover:after:bg-opacity-0 after:z-20 z-10 relative  ">

  <div class="card-content  p-5 rounded-lg rounded-b-none ">

    <div class="card-img rounded-md overflow-hidden relative z-10">
      <img class="w-full  " src="${game.thumbnail}" alt="">
      <div
        class="img-overlay absolute w-full h-full top-0 left-0 bg-[#212529] bg-opacity-40 z-20 group-hover:bg-opacity-0 transition-all duration-1000">
      </div>
    </div>

    <div class="card-game-head flex justify-between items-start mt-3 text-white">
      <h3>${game.title}</h3>
      <span
        class="bg-blue-300 group-hover:bg-blue-500 transition-all duration-1000  p-0.5 px-2 rounded-lg">Free</span>
    </div>

    <p class="text-center text-gray-500">${game.short_description.split(' ').slice(0, 10).join(' ')}
    </p>


  </div>



  <div class="card-footer flex justify-between items-center content-center border-t  border-[#212529] max-h-10  p-3 text-white ">
    <span class="bg-[#1a1e2083] p-1 px-2 rounded-lg text-xs">${game.genre}</span>
    <span class="bg-[#1a1e2083] p-1 px-2 rounded-lg text-xs">${game.platform}</span>

  </div>

</div>

`




    )

    // console.log(gamesHTML);

    document.querySelector('.games-cards').innerHTML = gamesHTML.join('')

  }


  async displayFirstGames(callback) {

    // games.geId(await games.fetchGames('MMORPG'))
    this.displayGames(await games.getGames('MMORPG'))

    await callback()
  }


  async changeCategory(anchors, callback) {


    //Change game Category
    for (let i = 0; i < anchors.length; i++) {

      anchors[i].addEventListener('click', async (event) => {

        event.preventDefault(true)
        Array.from(anchors).map((a) => {
          a.classList.remove('text-blue-300')
        })
        anchors[i].classList.add('text-blue-300')

        this.displayGames(await games.getGames(event.target.text))
        // console.log(await games.getGames(event.target.text));
        await callback()


      })
    }



  }


  
  getClickedCardId(cards, callback, callback2) {
    for (let i = 0; i < cards.length; i++) {
      cards[i].addEventListener('click', (event) => {

        const id = event.target.getAttribute('id')
        // console.log(id);
        callback(id, callback2)
        return id

      })

    }
  }



  showGameDetails(details) {

    document.querySelector('.game-details-section').classList.remove('hidden')
    document.body.classList.add('overflow-hidden')



    document.querySelector('.game-detail-content').innerHTML = `
  <div class="game-img ">
            <img src="${details.thumbnail}" class="w-full" alt="">

          </div>



          <div class="game-details col-span-2 text-white">


            <h3 class="game-title text-3xl mb-4"><span>Title: </span><span>${details.title}</span></h3>
            <p class="platform mb-4">Category: <span class="p-1 rounded-md bg-blue-500 text-black text-xs">${details.genre}</span></p>
            <p class="platform mb-4">Platform: <span class="p-1 rounded-md bg-blue-500 text-black text-xs">${details.platform}</span></p>
            <p class="status mb-4">Status: <span class="p-1 rounded-md bg-blue-500 text-black text-xs">${details.status}</span></p>
            <p class="mb-4">${details.description}</p>

            <a href="${details.freetogame_profile_url}" class="border border-yellow-600 rounded-md p-2 hover:bg-yellow-600 transition-all duration-500">Show
              Game</a>

          </div>

`



  }


  closeGameDetails() {



    document.querySelector('.close-icon').addEventListener('click', () => {

      document.querySelector('.game-details-section').classList.add('hidden')
      document.body.classList.remove('overflow-hidden')


    })

  }

  showNavMenu() {

    document.querySelector('.nav-menu-btn').addEventListener('click', () => {

      // document.querySelector('.nav-menu-list').classList.replace('hidden','flex')
      // document.querySelector('.nav-menu-list').classList.replace('flex','hidden')

      document.querySelector('.nav-menu-list').classList.toggle('hidden')

    })

  }

 
}