

export class Games {



    constructor() {
    }




    async getGames(categoryName) {


        const res = await fetch(`https://free-to-play-games-database.p.rapidapi.com/api/games?category=${categoryName}`, {
            method: 'GET',
            headers: {
                'x-rapidapi-key': '92028d7e57msh232a64eaf913af1p15ad44jsn99609b0d2f60',
                'x-rapidapi-host': 'free-to-play-games-database.p.rapidapi.com'
            }
        })
        if (res.ok) {
            document.querySelector('.loading-page').classList.add('hidden')
            document.body.classList.remove('overflow-hidden')
            return await res.json();

        }
        else {
            document.querySelector('.loading-page').classList.remove('hidden')
            document.body.classList.add('overflow-hidden')
        }


        // const { id } = await res.json();



    }




}