import { Games } from './games.js'
import { GameDetails } from './details.js'
import { UI } from './ui.js'





const games = new Games();
const gameDetails = new GameDetails()

const ui = new UI()





//Get Anchors
const anchors = document.querySelectorAll('a')
let cards = [];


//Diplay first Scene
ui.displayFirstGames(() => {



    getCards(() => {

        ui.getClickedCardId(cards, gameDetails.getGameDetailsById, ui.showGameDetails)

    })
})


//Change Games Category
ui.changeCategory(anchors, () => {

    getCards(() => {

        ui.getClickedCardId(cards, gameDetails.getGameDetailsById, ui.showGameDetails)


    })
})

ui.closeGameDetails()

ui.showNavMenu()

//Get All Cards
async function getCards(callback) {

    cards = await document.querySelectorAll('.card');
    // console.log(cards);

    await callback()
}



